import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { NotificationService } from 'src/app/shared/notification.service';

declare const FB: any;

if (!window['fbAsyncInit']) {
  window['fbAsyncInit'] = function() {
    FB.init({
      appId: '218887922272535',
      cookie: true,
      xfbml: true,
      version: 'v3.1',
    });

    FB.AppEvents.logPageView();
  };
}

function init(d, s, id) {
  const fjs = d.getElementsByTagName(s)[0];
  if (d.getElementById(id)) {
    return;
  }
  const js = d.createElement(s);
  js.id = id;
  js.src = 'https://connect.facebook.net/en_US/sdk.js';
  fjs.parentNode.insertBefore(js, fjs);
}

@Component({
  selector: 'app-fblogin',
  templateUrl: './fblogin.component.html',
  styleUrls: ['./fblogin.component.css'],
})
export class FbloginComponent implements OnInit {
  constructor(
    private notificationService: NotificationService,
    @Inject(DOCUMENT) private document: any
  ) {}

  async ngOnInit() {
    init(document, 'script', 'facebook-jssdk');
  }

  getLoginStatus() {
    FB.getLoginStatus(response => {
      console.log(response);
      if (response.status === 'connected') {
        const accessToken = response.authResponse.accessToken;
        this.notificationService.alert(accessToken);
      }
    });
  }

  login() {
    FB.login(
      response => {
        console.log(response);
        // Handle the response object, like in statusChangeCallback() in our demo code.
        if (response.status === 'connected') {
          // Logged into your app and Facebook.
          const accessToken = response.authResponse.accessToken;
          if (response.authResponse) {
            this.notificationService.alert(
              'Welcome!  Fetching your information.... '
            );
            FB.api('/me', me => {
              this.notificationService.alert(
                'Good to see you, ' + me.name + '.'
              );
            });
          } else {
            this.notificationService.alert(
              'User cancelled login or did not fully authorize.'
            );
          }
        } else {
          // The person is not logged into this app or we are unable to tell.
        }
      },
      { scope: 'public_profile,email' }
    );
  }

  logout() {
    FB.logout(response => {
      console.log(response);
    });
  }
}
